.emeDanger {
  padding: 1vh 3vw;
  background: #fafafa;

  .emeDanger-content {
    background: #fff;

    .list {
      padding: 2vw 2vw 2vw;

      .item {
        display: flex;
        align-items: center;
        padding: 0.2rem;
        color: #959292;

        img {
          width: 30px;
          height: 30px;
          vertical-align: text-bottom;
          margin-right: 10px;
        }

        span {
          padding-left: 1rem;
          color: #000;
        }
      }
    }
  }
}